
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:'login',
data () {
return {
formModel:{
username:'',
password:''
}
}
},
components: {
XButton
},
mounted(){
window.onkeyup = e => {
if(e.keyCode == 13){
this.handleLogin();
}
}
},
methods: {
handleLogin(){
if(!this.formModel.username){
this.$vux.toast.text('用户名不能为空！', 'top');
return false;
}else if(!this.formModel.password){
this.$vux.toast.text('密码不能为空！', 'top');
return false;
}
this.$vux.loading.show({
text: 'Loading'
})
this.$http.post({
url:'/login',
type:'json',
data:this.formModel,
success:data => {
localStorage.userInfo = JSON.stringify(data);
sessionStorage.username = data.realname;
sessionStorage.departmentId = data.departmentId;
sessionStorage.userId = data.id;
this.$vux.loading.hide();
this.$router.push({ path: '/main' }); 
},
error:msg => {
this.$vux.toast.text(msg, 'top');
this.$vux.loading.hide();
}
})
}
}
}



